.bclist {
  display: inline-block;
  text-align: left;
  padding: 0;
  width: 100%; }
  .bclist li {
    list-style: disc; }
  .bclist .actionable-item {
    position: relative; }
    .bclist .actionable-item svg {
      margin-left: 5px;
      opacity: 0.5; }
    .bclist .actionable-item .done {
      color: #28a745; }
