.breadcrumb {
  padding: 0;
  background: inherit;
  margin: 0; }
  .breadcrumb li {
    list-style: none;
    width: inherit;
    cursor: pointer;
    display: inline;
    padding: 0; }
    .breadcrumb li .logo {
      max-width: 32px; }
    .breadcrumb li:hover {
      color: #ffbf00; }
    .breadcrumb li:not(:first-child):before {
      content: ":";
      color: #ffbf00; }
