.bc-checkbox {
  padding: 5px;
  cursor: pointer;
  overflow: hidden; }
  .bc-checkbox:hover {
    background: #eee; }
  .bc-checkbox label {
    cursor: pointer;
    margin-bottom: 0px; }
  .bc-checkbox svg {
    margin-right: 10px; }
  .bc-checkbox button svg {
    margin-right: 0; }
  .bc-checkbox button.active {
    border: 1px #999999 solid; }
