.editable {
  cursor: pointer; }

.bc-filter:hover .display-component {
  padding: 0; }

.bc-filter:hover .placeholder {
  background: #f2f2f2;
  cursor: pointer;
  padding: 5px; }

.bc-filter .display-component {
  border: #cccccc solid 2px;
  display: inline-block;
  border-radius: 5px;
  padding: 5px;
  margin: 3px;
  position: relative; }

.bc-filter .options-component {
  border: #cccccc solid 2px;
  display: inline-block;
  border-radius: 5px;
  padding: 5px;
  margin: 3px;
  position: absolute;
  margin: 0;
  bottom: -10px;
  left: -1px;
  min-width: 250px;
  transform: translateY(100%);
  display: none; }
  .bc-filter .options-component p:first-child {
    text-align: right;
    margin: 0px 5px;
    padding: 0; }
  .bc-filter .options-component a {
    font-size: 80%; }
  .bc-filter .options-component ul {
    padding: 0;
    margin: 0; }
  .bc-filter .options-component ul li {
    padding: 0;
    margin: 0;
    border: #cccccc solid 2px;
    display: inline-block;
    border-radius: 5px;
    padding: 5px;
    margin: 3px; }
    .bc-filter .options-component ul li:hover {
      background: #f2f2f2;
      cursor: pointer; }
    .bc-filter .options-component ul li.selected {
      background: #dbe6f5; }
    .bc-filter .options-component ul li i:hover {
      background: #cccccc;
      border-radius: 5px; }

.bc-filter.open .options-component {
  display: inline-block; }
